@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

* {
  font-family: "Quicksand";
}

/* to make the whole page fit to screen 
and deal with background image*/
html { 
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  /* ../ means to access a folder outside of the current one*/
  background-image: url("../public/background.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}



body {
  height: 100%;
  margin: 0px;
}

.content {
  height: 100%;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  align-items: center;
  border-bottom: 2px solid #f2f2f2;
}

.navbar h1 {
  color: #000000;
}

.navbar .links {
  margin-right: 20px;
  
}

.navbar a {
  margin-left: 20px;
  margin-right: 80px;
  text-decoration: none;
  color: #000000;
}

.navbar a:hover {
  color: #9a8db8;
}


